.title {
    font-weight: 200;
    text-transform: uppercase;
    text-align: center;
    font-size: 48px;
}



.ul {
    display: flex;
    justify-content: center;
    flex-direction: column; 
    align-items: center;
    list-style-type: none;
}


.li {  
    font-size: 28px;
    color: #00a2ff;
    border: 2px solid #ccc;
    font-family: monospace;
    margin: 30px;
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
}


.link {
    padding: 10px 100px; 
    text-decoration: none;
}


.input {
    padding: 15px 20px;
    font-size: 18px;
    margin: 20px;
}


.button {
    padding: 10px 120px;
    border-radius: 20px 20px;
    font-size: 18px;
    font-weight: 300;
    background-color: rgb(1, 166, 243);
    color: rgb(0, 0, 0);
}


.remove {
    font-size: 18px;
    font-weight: 300;
    padding: 20px;
}